<template>
  <div class="page-teacherQrCode">
    <BindPhone></BindPhone>
    <!-- 底部内容 -->
    <div class="static-box mt__50">
      <div class="content">
        <img class="static-img" :src="require('@/assets/Slim/book.png')" />
      </div>
      <div class="ml__10">已付款</div>
    </div>
    <div class="mt__20 git-box">
      <img :src="require('@/assets/Slim/down.gif')" />
    </div>
    <div class="content-title mt__20">请务必扫码添加, 否则无法学习</div>
    <!-- 二维码 -->
    <div class="qrCode-box">
      <img :src="qrcodeData.qrcode[0]" ref="qrcode" />
    </div>
    <div class="qrCode-text">长按识别二维码, 添加老师微信</div>
    <div class="qrCode-topic">*老师会在24小时内通过好友申请, 不会影响学习</div>
    <div
      class="tipsError"
      v-for="item in qrcodeData.errs"
      :key="item.order_num">
      订单号：{{ item.order_num }} {{ item.msg }}
    </div>
    <div class="qrcode_tips" @click="showKefu">有问题? 联系客服</div>
    <div class="tips">
      <img src="@/assets/Slim/tips.jpg" alt="" />
    </div>
    <VanDialog
      class="dialog"
      v-model="show"
      :show-confirm-button="false">
      <div class="title">联系客服</div>
      <div class="mt__20 git-box">
        <img :src="require('@/assets/Slim/down.gif')" />
      </div>
      <div class="text">在公众号后台留言给客服即可</div>
      <img
        src="https://xiaobai-market.oss-cn-huhehaote.aliyuncs.com/Public/xun/qrcode/code.jpg"
        class="kefuCode"
        alt="" />
      <div class="code_text">长按识别二维码, 关注公众号</div>
      <div class="code_topic">*温馨提示：客服在线时间早10点-晚6点</div>
      <div class="button" @click="close">知道了</div>
    </VanDialog>
  </div>
</template>

<script>
import BindPhone from './components/BindPhone'
import wxAuth from '@/helper/Auth/wxAuth'
import { postBack } from '@/helper/payConfig'
import { mapState } from 'vuex'
import { getSearch } from '@/utils'
import store from '@/store'
export default {
  components: { BindPhone },
  data () {
    return {
      show: false,
      teacherCodeImg: '' // 老师二维码
    }
  },
  async beforeRouteEnter (to, from, next) {
    await wxAuth({ getTokenQuery: postBack() })
    const mid = store.state.user?.userInfo?.id
    store.$log.update({ mid })
    next()
  },
  computed: {
    ...mapState('user', ['userInfo'])
  },
  mounted () {
    this.$log.addLongEvent(this.$refs.qrcode, (e) => {
      const search = getSearch()
      this.$log.long({
        current_page: location.href,
        module_ori: '老师二维码',
        module_part: '106_luodiyehoutai_lcl_0025'
      })
      this.$apis.userOperator.pressClassQr(search)
    })
  },
  async created () {
    wx.ready(() => {
      wx.hideAllNonBaseMenuItem()
    })
    this.$log.update({
      mid: this.userInfo.id
    })
    this.getQrcode()
  },
  methods: {
    close () {
      this.show = false
    },
    showKefu () {
      this.show = true
      this.$log.click({
        module_ori: '7天体验营报名成功页-点击查看客服',
        module_part: '128_cqssxk_h5_00081'
      })
    },
    getQrcode () {
      const data = this.$route.query.data
      this.qrcodeData = JSON.parse(data)
    }
  }
}
</script>

<style lang="scss">
.page-teacherQrCode {
  .dialog {
    overflow: visible;
    padding-bottom: 50px;
    // background: transparent;
    text-align: center;
    .title {
      height: 116px;
      background: #2f6e6d;
      border-radius: 30px 30px 0 0;
      line-height: 116px;
      text-align: center;
      font-size: 48px;
      font-family: PingFang SC;
      font-weight: 500;
      color: #ffffff;
    }
    .text {
      font-size: 36px;
      font-family: PingFang SC;
      font-weight: 500;
      color: #f62724;
      text-align: center;
      margin: 10px 0;
    }
    .kefuCode {
      width: 302px;
      height: 302px;
      background: #8e8e8e;
      margin: 20px 0;
      margin-bottom: 5px;
    }
    .code_text {
      font-size: 26px;
      font-family: PingFang SC;
      font-weight: 400;
      color: #787878;
      line-height: 36px;
    }
    .code_topic {
      width: 560px;
      height: 50px;
      background: #cadede;
      opacity: 0.8;
      border-radius: 25px;
      font-size: 24px;
      font-family: PingFang SC;
      line-height: 50px;
      margin: 30px auto;
      font-weight: 400;
      color: #008d6f;
    }
    .button {
      width: 220px !important;
      height: 66px;
      border: 2px solid #d9ffe6;
      border-radius: 33px;
      font-size: 38px;
      font-family: PingFang SC;
      font-weight: 400;
      color: #d9ffe5;
      position: absolute;
      bottom: -150px;
      left: 50%;
      line-height: 66px;
      transform: translateX(-50%);
    }
  }
  .tips {
    width: 750px;
    height: 868px;

    background: #cadede;
    box-sizing: border-box;
    padding: 30px;
    img {
      width: 100%;
    }
  }
  // 付款盒子
  .static-box {
    @include horVerCenter;
    .content {
      width: 49px;
      height: 53px;
      overflow: hidden;
    }
    .static-img {
      width: 49px;
      height: 53px;
    }
    div {
      font-size: 36px;
      font-weight: 400;
      color: #3a3a52;
    }
  }
  .git-box {
    @include horVerCenter;
    img {
      width: 35px;
      height: 35px;
    }
  }
  // 内容标题
  .content-title {
    @include horVerCenter;
    font-size: 36px;
    font-weight: 500;
    color: #f62724;
  }
  .qrCode-box {
    margin: 30px auto;
    width: 30vw;
    height: 30vw;
    img {
      width: 100%;
      height: 100%;
    }
  }
  .qrCode-text {
    text-align: center;
    font-size: 26px;
    font-weight: 400;
    color: #787878;
  }
  .qrcode_tips {
    font-size: 26px;
    font-family: PingFang SC;
    font-weight: 400;
    text-decoration: underline;
    color: #3a3a52;
    line-height: 36px;
    text-align: center;
    margin: 52px auto;
  }
  .tipsError {
    // width: 668px;
    height: 24px;
    font-size: 23px;
    font-family: PingFang SC;
    font-weight: 400;
    color: #306e6e;
    line-height: 40px;
    text-align: center;
  }
  .qrCode-topic {
    margin: 52px auto;
    margin-bottom: 20px;
    width: calc(100% - 150px);
    height: 50px;
    background: #ffeaea;
    opacity: 0.8;
    border-radius: 25px;
    font-size: 24px;
    font-weight: 400;
    background: #e7fbf6;
    color: #008d6f;
    @include horVerCenter;
  }
}
</style>
